import createMiddleware from 'next-intl/middleware';
import { NextRequest, NextResponse } from 'next/server';
import { locales, defaultLocale } from './i18n';

const intlMiddleware = createMiddleware({
  locales,
  defaultLocale,
  localePrefix: 'always'
});

export default function middleware(request: NextRequest) {
  const { pathname } = request.nextUrl;
  
  // Check if there's a locale cookie preference
  const localeCookie = request.cookies.get('NEXT_LOCALE')?.value;
  
  // If accessing root without locale, check cookie for preference
  if (pathname === '/') {
    const preferredLocale = localeCookie && locales.includes(localeCookie as any) 
      ? localeCookie 
      : defaultLocale;
    
    return NextResponse.redirect(new URL(`/${preferredLocale}`, request.url));
  }
  
  // Let intl middleware handle everything else
  const response = intlMiddleware(request);
  
  // Set locale cookie based on current locale in URL
  const pathnameLocale = locales.find(
    locale => pathname.startsWith(`/${locale}/`) || pathname === `/${locale}`
  );
  
  if (pathnameLocale && response) {
    response.cookies.set('NEXT_LOCALE', pathnameLocale, {
      maxAge: 60 * 60 * 24 * 365, // 1 year
      path: '/'
    });
  }
  
  return response;
}

export const config = {
  // Match all pathnames except for
  // - API routes
  // - _next (Next.js internals)
  // - Static files
  matcher: ['/((?!api|_next|.*\\..*).*)']
};

